<template>
	<view :class="nightStatus?'nightTheme' : ''" style="min-height: 100vh;">

		<!-- #ifndef MP -->
		<page-title :theme="nightStatus ? 'nightTheme' : 'bg-white'">音乐详情</page-title>
		<!-- #endif -->
		<view class="flex align-center justify-center flex-column py-4">
			<view>
				<text class="font">歌曲:</text>
				<text class="font-weight-bold">{{audioName}}</text>
			</view>
			<view class="mt-1 font">
				<text class="font">演唱:</text>
				<text class="font-weight-bold">{{singerName}}</text>
			</view>
		</view>
		<!-- 封面 -->
		<view class="flex align-center justify-center" style="height: 320rpx;">
			<image class="rounded-lg" style="width: 80%; box-shadow: 0 2rpx 6rpx 0; height: 100%;" src="../../static/images/1.webp"
				lazy-load mode="aspectFill"></image>
		</view>

		<view class="flex align-center justify-center font" style="height: 65rpx;">
			<!-- 总时长 -->
			<view class="">
				{{durationTime | formatMusicTime}}
			</view>
			<!-- 进度条 -->
			<view class="" style="width: 500rpx;">
				<slider block-size="16" activeColor="#003366" block-color="#003366"
					:max="durationTime ? durationTime : 0" :value="currentTime" @change="slideToPlay"
					@changing="slideToPlay" />
			</view>
			<!-- 播放时刻 -->
			<view class="">
				{{currentTime | formatMusicTime}}
			</view>
		</view>

		<!-- 按钮部分 -->
		<view class="">
			<view class="flex align-center justify-center" style="padding-top: 60rpx;">
				<view class="mr-3 animate__animated " hover-class="animate__pulse">
					<iconfont iconId="icon-shangyixiang" iconSize="85" @click="preOrNext('pre')"></iconfont>
				</view>
				<view class="mx-5">
					<iconfont :iconId="!playState ?  'icon-bofang1' : 'icon-zanting'" iconSize="80"
						@click="playOrPause"></iconfont>
				</view>
				<view class="ml-2 animate__animated " hover-class="animate__pulse">
					<iconfont iconId="icon-xiayixiang" iconSize="85" @click="preOrNext('next')"></iconfont>
				</view>
			</view>
			<view class="flex align-center justify-center font " style="padding-top: 60rpx;">
				<view class="flex flex-column align-center">
					<iconfont :iconId="!listStatus ? 'icon-icon--' : 'icon-liebiao'" :iconSize="60"
						@click="changeStatus('listStatus')"></iconfont>
					<text class="pt-1" @click="changeStatus('listStatus')">播放列表</text>
				</view>
				<view class="flex flex-column align-center" style="padding: 0 80rpx;">
					<iconfont :iconId="!collectStatus?'icon-aixinfengxian' : 'icon-xihuan2'" iconSize="60"
						@click="changeStatus('collectStatus')"></iconfont>
					<text class="pt-1" @click="changeStatus('collectStatus')">收藏</text>
				</view>
				<view class="flex flex-column align-center">
					<iconfont :iconId="!nightStatus ? 'icon-yejianmoshi':'icon-yueliang'" iconSize="60"
						@click="changeStatus('nightStatus')"></iconfont>
					<text class="pt-1" @click="changeStatus('nightStatus')">夜间模式</text>
				</view>
			</view>

		</view>


		<!-- 歌手信息 -->
		<view v-if="!listStatus" class="fixed-bottom  p-2  shadow animate__animated animate__fadeIn"
			style="height: 260rpx;border-radius: 30rpx;z-index: 0;">
			<view class="flex justify-between">
				<view class="">
					<view>
						<text class="font">歌曲:</text>
						<text class="font">{{audioName}}</text>
					</view>
					<view class="mt-1">
						<text class="font">演唱:</text>
						<text class="font">{{singerName}}</text>
					</view>
				</view>
				<iconfont iconId="icon-jieshao" iconSize="65" @click="showSingerIntro"></iconfont>
			</view>
			<view class="">
				<view class="font-md pt-2">
					歌手简介:
				</view>
				<view class="text-ellipsis" style="width: 710rpx;">
					{{singerIntro}}
				</view>
			</view>
		</view>

		<!-- 播放列表 -->
		<view v-else class="fixed-bottom  p-2  shadow animate__animated animate__fadeInUp"
			style="height: 500rpx;border-radius: 30rpx;" :class="nightStatus ? 'nightTheme': 'bg-white'">
			<view class="font-weight-bold font-md flex align-center" style="height: 50rpx;">
				列表选择 <iconfont class="ml-auto" iconId="icon-diandian" @click="changeStatus('listStatus')"></iconfont>
			</view>
			<scroll-view scroll-y style="height: 350rpx;">

				<view v-for="(item,index) in audioList" :key="item.id" class="flex align-center font px-2"
					style="height: 85rpx;" @click="selectPlay(item.id)">
					<text class="flex-1 text-ellipsis">{{item.audioName}}</text>
					<text class="flex-1 text-ellipsis">{{item.singerName}}</text>
					<view class=" flex-1 ml-3 flex align-center justify-end">
						<text class="mr-1">播放</text>
						<iconfont iconId="icon-bofangsanjiaoxing" iconSize="40"></iconfont>
					</view>
				</view>

			</scroll-view>
		</view>

		<!-- 歌手简介详情 -->
		<uni-popup ref="popup">
			<view class="px-2 py-1 shadow rounded" style="width: 600rpx;height: 800rpx;"
				:class="nightStatus ? 'nightTheme':'bg-white'">
				<text>{{singerIntro}}</text>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import pageTitle from '@/components/page-title.vue';
	import uniPopup from "@/components/uni-popup/uni-popup.vue";
	import unit from '@/common/unit.js'
	import {
		mapState,
		mapGetters,
		mapMutations,
		mapActions
	} from 'vuex';
	export default {
		components: {
			pageTitle,
			uniPopup,
		},
		filters: {
			formatMusicTime(num) {
				return unit.formatMusicTime(num);
			}
		},
		data() {
			return {
				listStatus: false,
				collectStatus: false,
				nightStatus: false,
			}
		},
		computed: {
			...mapState({
				durationTime: state => state.audio.durationTime,
				currentTime: state => state.audio.currentTime,
				audioList: state => state.audio.audioList,
				playState: state => state.audio.playState,
			}),
			...mapGetters(['audioName', 'singerName', 'singerIntro']),
		},
		onLoad() {
			this.init();
		},
		methods: {
			...mapMutations([]),
			...mapActions([
				'slideToPlay',
				'playOrPause',
				'preOrNext',
				'init',
				'selectPlay'
			]),
			// 改变列表状态
			changeStatus(statusType) {
				this[statusType] = !this[statusType];
			},
			// 展示歌手简介
			showSingerIntro() {
				this.$refs.popup.open();
			}
		}
	}
</script>

<style>

</style>
